import { Mark } from '@ali/4ever-cangjie';

interface Bold extends Mark {
  type: 'bold',
};

const Bold = {
  TYPE: 'bold',

  create(bold?: boolean): Bold {
    if(bold === false) {
      return Mark.create({ 
        type: Bold.TYPE,
        data: {
          value: false,
        }
      }) as Bold;
    } else {
      return Mark.create({ type: Bold.TYPE }) as Bold;
    }
  },

  isBold(maybeBold: any): maybeBold is Bold {
    return Mark.isMark(maybeBold) && maybeBold.type === Bold.TYPE;
  },
};

export default Bold;
